import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '*',
            redirect: '/'
        },
        {
            path: '/',
            name: '_home',
            redirect: '/home',
        },
        {
            path: '/home',
            name: 'home',
            component:  resolve => require(['@/view/miuce/home'], resolve),
        },
    ]
})



